body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

header {
  background-color: #282c34;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 1vmin);
  color: white;
  display: grid;
  padding: 5px;
  grid-template-columns: repeat(2, 1fr);
}

header > h2 {
  text-align: left;
  padding-left: 30px;
}

header > div {
  text-align: right;
  padding-right: 30px;
}

.dashboard {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.dash_component {
  display: grid;
  grid-template-rows: 1fr 2fr;
}

.details_component {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.card {
  color: black;
  background: #ededed;
  padding: 10px 30px;
  margin: 20px;
  border-radius: 10px;
}

.card > h4 {
  font-weight: 400;
}

.vis-network {
  overflow: visible;
}

.db_stats_text {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: left;
  font-size: 0.9rem;
}

.db_stats_text > div > label {
  font-style: italic;
}

.db_stats_text > div > span {
  font-weight: bold;
  padding-left: 5px;
}
